import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminComponent } from './admin.component';

const routes: Routes = [
    {
        path: '',
        component: AdminComponent,
        
        children: [
           
            {
                path: 'overview',
                loadChildren: () => import('./overview/overview.module').then((m) => m.OverviewModule)
            },
            {
                path: 'customer-care-assignment',
                loadChildren: () => import('./customer-care-assignment/customer-care-assignment.module').then((m) => m.CustomerCareAssignmentModule)
            },
            {
                path: 'salesman-assignment',
                loadChildren: () => import('./salesman-assignment/salesman-assignment.module').then((m) => m.SalesmanAssignmentModule)
            },
            {
                path: 'customer-by-app',
                loadChildren: () => import('./customer-by-app/customer-by-app.module').then((m) => m.CustomerByAppModule)
            },
            {
                path: 'partner-list',
                loadChildren: () => import('./partner/partner-list/partner-list.module').then((m) => m.PartnerListModule)
            },
            {
                path: 'carestaff-class',
                loadChildren: () => import('./partner/carestaff-class/carestaff-class.module').then((m) => m.CarestaffClassModule)
            },
            {
                path: 'carestaff-commission',
                loadChildren: () => import('./partner/carestaff-commission/carestaff-commission.module').then((m) => m.CarestaffCommissionModule)
            },
            {
                path: 'salesman-commission',
                loadChildren: () => import('./partner/salesman-commission/salesman-commission.module').then((m) => m.SalesmanCommissionModule)
            },
            
            {
                path: 'salesman-class',
                loadChildren: () => import('./partner/salesman-class/salesman-class.module').then((m) => m.SalesManClassModule)
            },
            {
                path: 'dealer',
                loadChildren: () => import('./dealer/dealer.module').then((m) => m.DealerModule)
            },
            {
                path: 'data-skhdt',
                loadChildren: () => import('./data-skhdt/data-skhdt.module').then((m) => m.DataSKHDTModule)
            },
            {
                path: 'accountant',
                loadChildren: () => import('./accountant/accountant.module').then((m) => m.AccountantModule)
            },
        ]
    },
];
@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoutingModule { }
